import { GamesEnum } from './../../shared/models/game_play.model';
import { GlobalDataService } from './../../shared/services/globalData/global-data.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { RouterConstants } from 'src/app/shared/config/constants/router.constants';
import { IonSlides } from '@ionic/angular';

@Component({
  selector: 'app-game-list',
  templateUrl: './game-list.page.html',
  styleUrls: ['./game-list.page.scss'],
})
export class GameListPage implements OnInit {
  @ViewChild(IonSlides) slides: IonSlides;

  elements: Array<any> = new Array<any>();
  totalElements: number;
  loadingContent: boolean;

  currentElement: any;
  slideOpts = {
    initialSlide: 0,
    // speed: 400,
    effect: "coverflow",
    spaceBetween: 30,
    slidesPerView: 1,
    // centeredSlides: false,
    // loop: false,
    // autoplay: false,
    // width: 168
  };
  GamesEnum = GamesEnum;
  constructor(
    private router: Router,
    public globalData: GlobalDataService,
  ) { }

  ngOnInit() {
    this.elements = [
      { name: 'Ruleta', code: GamesEnum.Roulette, disabled: false },
      { name: 'Dados (lo eliminarÃ©)', code: GamesEnum.DICES, disabled: false },
      { name: '3 en raya', code: GamesEnum.TICTACTOE, disabled: false },
      { name: 'Memory', code: GamesEnum.MEMORY, disabled: false },
      { name: 'Rasca Rasca (passant de ronda)', code: GamesEnum.SCRATCH, disabled: true },
      { name: 'Cookie clicker', code: GamesEnum.COOKIE, disabled: false },
      { name: 'Preguntados (si trobo api)', code: GamesEnum.QUEST, disabled: true },
      { name: 'Snake', code: GamesEnum.SNAKE, disabled: true},
    ]
    // this.getUserRewards();
  }

  ionViewWillEnter(){
    this.changeSlide();
  }

  slideLoaded() {
    console.log('Loaded slides')
  }

  changeSlide(event?) {
    this.slides.getActiveIndex().then(index => {
      console.log(index);
      this.currentElement = this.elements[index];
   });
  }

  openGame() {
    console.log('Detail', this.currentElement);
    this.globalData.initGlobalData(this.currentElement.code)
    // this.router.navigateByUrl(RouterConstants.CHECK_BEACON+ element.id, {state: element.selectedElement});
    this.router.navigateByUrl(RouterConstants.CHECK_BEACON);
  }

}